.container {
	min-width: 300px;
	padding: 20px 25px;
	max-height: 75vh;
	max-width: 75vw;

	.buttonContainer {
		padding-top: 25px;
		display: flex;
		justify-content: flex-end;
		// max-height: 20px;
	}
	.dataContainer {
		overflow: auto;
		min-width: 300px;
		padding: 20px 25px;
		max-height: calc(75vh - 125px);
		max-width: 75vw;
	}
	.button {
		margin-left: 20px;
		font-size: 0.8em;
	}
}
